export const landingSlice = createSlice({
  name: "landing",
  initialState,
  reducers: {
    setCurrentLanguage: (state, action) => {
      state.currentLanguage = action.payload;
    },
  },
});

export const { setCurrentLanguage } = landingSlice.actions;

